* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  width: 95vw;
  height: 95vh;
  background: linear-gradient(to right, #0621ee, #f9f505) 0 0 / cover;
  font-family: Raleway, Tahoma, Arial, sans-serif;
  font-size: 13px;
  line-height: 1.5;
}

h1, h2, h3, h4, h5, p, a, ul, ol {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

li {
  list-style-type: none;
}

.spinner-border {
  width: 30px;
  height: 30px;
  border: .25em solid;
  border-right-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  animation: spinner-border .75s linear infinite;
  display: block;
}

@keyframes spinner-border {
  to {
    transform: rotate(360deg);
  }
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

input {
  height: 42px;
  color: #212121;
  background-color: rgba(255, 255, 255, .8);
  border: 0;
  border-radius: 4px;
  outline: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 0 16px;
  line-height: 42px;
  display: block;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 2px rgba(0, 0, 0, .24);
}

input:focus {
  background-color: #fff;
  outline: 0;
}

.country-info {
  width: 800px;
  font-size: 2.5rem;
}

.flag {
  width: 3em;
  height: 2em;
  margin-right: 5px;
  display: block;
}

.country-list {
  margin-left: auto;
